<template>
    <v-container>
        <v-row class="my-3">
            <v-col cols="12">
                <h1
                    class="text-center"
                    style="color: #A51417"
                >Found or Lost Items? Check below items!</h1>
                <p class="text-center">
                    For more features, please download our app:
                    <v-btn
                        small
                        dark
                        outlined
                        color="#A51417"
                        href="https://apps.apple.com/us/app/bearfound/id1503739627"
                        target="_blank"
                    >Download</v-btn>
                </p>
            </v-col>
        </v-row>
        <v-row justify="center" align="center" class="mb-7">
            <v-col cols="12">
                <v-card elevation="5" :loading="loading && 'error'">
                    <v-tabs centered v-model="tab" background-color="#007360" dark icons-and-text>
                        <v-tabs-slider></v-tabs-slider>
                        <v-tab href="#tab-1">
                            Lost Items
                            <v-icon small>fas fa-search</v-icon>
                        </v-tab>
                        <v-tab href="#tab-2">
                            Found Items
                            <v-icon small>fas fa-binoculars</v-icon>
                        </v-tab>
                    </v-tabs>
                    <v-tabs-items v-model="tab">
                        <v-tab-item value="tab-1" style="min-height: 300px;" class="pa-5">
                            <v-row>
                                <v-overlay :absolute="absolute" :value="loading">
                                    <v-progress-circular indeterminate :width="7"></v-progress-circular>
                                </v-overlay>
                                <v-col
                                    cols="3"
                                    v-for="(lost, index) in lostItems"
                                    :key="index + 'A'"
                                >
                                    <v-hover v-slot:default="{ hover }">
                                        <v-card :elevation="hover? 15 : 5">
                                            <v-card-title style="color: #a51417">{{ lost.name }}</v-card-title>
                                            <v-card-subtitle>{{ lost.date | filtered }}</v-card-subtitle>
                                            <v-img
                                                transition="scale-transition"
                                                class="elevation-5"
                                                :src="lost.img"
                                                height="200"
                                            ></v-img>
                                            <v-card-text>{{ lost.detail }}</v-card-text>
                                            <v-card-actions>
                                                <v-chip
                                                    small
                                                    dark
                                                    color="#007360"
                                                >{{ lost.category }}</v-chip>
                                            </v-card-actions>
                                        </v-card>
                                    </v-hover>
                                </v-col>
                            </v-row>
                        </v-tab-item>
                        <v-tab-item value="tab-2" style="min-height: 300px;" class="pa-5">
                            <v-row>
                                <v-col
                                    cols="3"
                                    v-for="(lost, index) in foundItems"
                                    :key="index + 'A'"
                                >
                                    <v-hover v-slot:default="{ hover }">
                                        <v-card :elevation="hover? 15 : 5">
                                            <v-card-title style="color: #a51417">{{ lost.name }}</v-card-title>
                                            <v-card-subtitle>{{ lost.date | filtered }}</v-card-subtitle>

                                            <v-card-text>{{ lost.detail }}</v-card-text>
                                            <v-card-actions>
                                                <v-chip
                                                    small
                                                    dark
                                                    color="#007360"
                                                >{{ lost.category }}</v-chip>
                                            </v-card-actions>
                                        </v-card>
                                    </v-hover>
                                </v-col>
                            </v-row>
                        </v-tab-item>
                    </v-tabs-items>
                </v-card>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
    import axios from "axios";
    const firebase = require("firebase");
    import moment from "moment";

    // Required for side-effects
    require("firebase/firestore");
    export default {
        data() {
            return {
                tab: null,
                lostItems: [],
                loading: false,
                absolute: true,
                img: null,
                foundItems: []
            };
        },
        mounted() {
            this.loading = true;
            this.getFirebase();
        },
        filters: {
            filtered(newValue) {
                return moment.unix(newValue.seconds).format("DD MMMM YYYY, HH:mm");
            }
        },
        methods: {
            getFirebase() {
                // Initialize Cloud Firestore through Firebase
                firebase.initializeApp({
                    apiKey: "AIzaSyD1Ji7REPKpoLwak2zH5ytDl4EpTWqgc34",
                    authDomain: "bearfound-ebae0.firebaseapp.com",
                    databaseURL: "https://bearfound-ebae0.firebaseio.com",
                    projectId: "bearfound-ebae0",
                    storageBucket: "bearfound-ebae0.appspot.com"
                });

                var db = firebase.firestore();
                var storage = firebase.storage().ref();

                db.collection("objects")
                    .get()
                    .then(querySnapshot => {
                        querySnapshot.forEach(doc => {
                            storage
                                .child(doc.data().image_referece)
                                .getDownloadURL()
                                .then(url => {
                                    let merged_object = Object.assign(doc.data(), {
                                        img: url
                                    });
                                    this.lostItems.push(merged_object);
                                });
                        });
                    });

                db.collection("lost")
                    .get()
                    .then(querySnapshot => {
                        querySnapshot.forEach(doc => {
                            this.foundItems.push(doc.data());
                        });
                    })
                    .finally(() => {
                        this.loading = false;
                    });
            }
        }
    };
</script>

